/*  
* Footer 
*/

.footer {
    background-color: rgb(41, 41, 41);
    padding: 0 20px;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: top;
        flex-wrap: wrap;
    }
    &__column {
        margin: 0 30px 0 0;
        text-align-last: left;
        text-transform: none;
    }
    &__contacts {
        display: flex;
        align-items: center;
        img {
            margin-right: 10px;
        }
    }
    &__text {
        max-width: 300px;
        line-height: 1.5;
        margin-bottom: 30px;
    }
    &__social {
        margin: 20px 0;
        img {
            margin-right: 30px; 
            width: 40px;
        }
    }
    &__menu {
        padding: 0;
        &-link {
            line-height: 2;
            &:hover {
                color: $color;
            } 
        }
    }
    &__form-input {
        padding: 15px;
        margin-bottom: 15px;
        max-width: 80%;
        border: none;
    }
}