/*  
* Slider
*/

.tour,
.place,
.team { 
    &-slider {
        padding-top: 110px;
        &__info {
            margin-top: 50px;
        }
        .slider-arrows {
            top: 50px;
        }
        .slick-slide:nth-child(4n + 1) {
            .tour-box,
            .place-box,
            .team-box {
                margin-top: 32px;
            }
        }
        .slick-slide:nth-child(4n + 3) {
            .tour-box,
            .place-box,
            .team-box {
                margin-top: 89px;
            }
        } 
    }
    &-box {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        &__inner {
            cursor: pointer;
            min-height: 750px;
            background: rgba(#1c2121, .85);
            position: relative;
            transition: all .5s;
            &:hover {
                background: rgba(#1c2121, .3);
                .tour-box__inner-btn,
                .place-box__inner-btn,
                .team-box__inner-btn {
                    display: block;
                }
                .tour-box__inner-title,
                .place-box__inner-title,
                .team-box__inner-title,
                .tour-box__inner-subtitle,
                .place-box__inner-subtitle,
                .team-box__inner-subtitle {
                    display: none;
                }
            }
            &-title {
                font-size: 40px;
                line-height: 36px;
                font-weight: 800;
                padding: 72px 0 0 40px;
                width: 140px;
            }
            &-subtitle {
                font-size: 18px;
                line-height: 22px;
                letter-spacing: 8px;
                transform: rotate(-90deg);
                position: absolute;
                left: 40px;
                bottom: 80px;
                width: 0;
                height: 0;
                white-space: nowrap;
                span{
                    font-weight: 100;
                }
            }
            &-btn {
                position: absolute;
                bottom: 0;
                text-align: center;
                left: 0;
                right: 0;
                display: none;
            }
        } 
    }
}