/*  
* Media
*/

@media (max-width: 1100px) {
    .dots-box {
       display: none;
    }
    .slider {
        &-arrows {
            bottom: 50px;
            right: 20px;
            &-left {
                right: 80px; 
            }
        }
        &-item {
            &__info {
                &-number {
                    display: block;
                }
            }
        }
    }
}
@media (max-width: 960px){
    .travel-slider__info-title {
        font-size: 30px;
    }
    .contact {
        &-container {
            width: 100%;
            padding: 0;
        }
        &__form {
            padding: 30px 0;
            width: 60%;
        }
    }
}
@media (max-width: 769px){
    .header {
        &__title {
            font-size: 80px;
        }
        &__aside {
            border-right: none; 
        }
    }
    .menu-btn {
        display: block;
    }
    .menu {
        width: 100vw;
        height: 100vh;
        background-color: #000;
        z-index: 22;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 16px;
        transform: translateX(-100%);
        transition: all .5s;
        &.active{
            transform: translateX(0);
        }
    }
    .services {
        &__inner {
            flex-wrap: wrap;
        }
        &-item {
            width: 60vw;
            margin: 0 auto;
            text-align: center;
            margin-bottom: 80px;
            p {
                width: 40vw;
                margin: 0 auto;
                text-align: justify;
            }
        }
    }
    .title {
        span {
            font-size: 100px;
        }
        h3 {
            font-size: 50px;
        }
    }
    .blog {
        &__inner {
            display: flex; 
        }
        &__left {
            width: 70vw;
            margin: 0 50px 0 25px;
            &-item {
                margin-bottom: 50px;
                text-transform: none;            
            }
        }
        &__right {
            width: 30vw;
            &-item {
                display: block;
                img {
                    margin-right: 0;
                    width: 100%;
                }
            }
            span {
                display: inline-block;
                margin: 0 10px 10px 0;
            }
        }
        &-slider__btn {
            margin-left: 25px;
        }
    }
    .slider {
        &-item {
            &__info {
                right: 20px;
                bottom: 115px;
                &-title {
                    font-size: 80px;
                }
                &-text {
                    font-size: 40px;
                }
            }
        }
    }
}
@media (max-width: 600px) {
    .map-info {
        padding: 0;
        margin: 5px;
        width: 4em;
    }
    .travel {
        &-slider {
            &__info {
                display: block;
                text-align: center;
                margin: 0 20px;
            }
            &__text {
                width: 80vw;
            }
        }
    }
    .hotel { 
        &-slider {
            &__info {
                margin-top: 100px;
                display: block;
                &-item {
                    margin: 0 auto;
                    margin-bottom: 30px;
                }
                &-title {
                    padding-left: 60px;
                }
            }
        }
    }
}
@media (max-width: 460px){
    .section {
        padding: 50px 0;
    }
    .header {
        &__map {
            display: none;
        }
        &__arrows {
            left: 40px;
            bottom: 50px;
        }
    }
    .slider {
        &-item {
            &__info {
                right: 20px;
                bottom: 150px;
                &-title {
                    font-size: 50px;
                }
            }
        }
        &-arrows {
            bottom: 50px;
            right: 20px;
            &-left {
                right: 80px; 
            }
        }
    }
    .services-item {
        width: 80vw;
        margin-bottom: 80px;
        p {
            width: 60vw;
            text-align: center;
        }
    }
    .title {
        span {
            font-size: 60px;
        }
        h3 {
            font-size: 40px;
        }
    }
    .blog {
        &__inner {
            display: block; 
            margin-bottom: 50px;
        }
        &__left {
            width: 90vw;
            margin: 0 auto;
            margin-bottom: 100px;
            &-item {
                h2 {
                    width: 100%;
                }
                margin-bottom: 50px;
                text-transform: none;            
            }
        }
        &__right {
            width: 90vw;
            margin: 0 auto;
            &-item {
                display: block;
                img {
                    margin-right: 0;
                    width: 100%;
                    height: 200px;
                }
            }
            span {
                display: block;
                margin: 0 10px 10px 0;
            }
        }
        &-slider__btn {
            margin: 0;
        }
    }
    .map {
        &-info {
            display: none; 
        }
        &-label {
            width: 10px;
            height: 10px;
        }
    }
    .contact {
        padding: 0;
        &-container {
            width: 100%;
            padding: 0;
        }
        &__form {
            padding: 30px 0;
            width: 90%;
        }
    }
}
