/*  
* Map
*/

.map {
    position: relative;
    &-label {
        position: absolute;
        width: 1em;
        height: 1em;
        background-color: $color;
        border-radius: 50%;
        cursor: pointer;
        transition: all 0.5s;
    }
    &-info {
        position: absolute;
        z-index: 1;
        padding: 10px 15px;
        border: 0;
        background-color: rgba(0,0,0,0.3);
        box-shadow: none;
        margin: 5px;
        width: 6em;
        text-align: center;
    }
    &-title {
        letter-spacing: 0.16em;
        color: $color;
        font-size: 10px;
    }
}
.img-responsive,
.thumbnail>img,
.thumbnail a>img,
.carousel-inner>.item>img,
.carousel-inner>.item>a>img {
    display: block;
    max-width: 100%;
    height: auto;
}
