/*  
* Sections
*/

/* 1. Services */
.services {
    &__inner {
        display: flex;
    }
    &-item {
        margin: 50px;
        text-align: center;
        img {
            width: 60px;
            height: 60px;  
            margin-bottom: 20px;  
        }
        p {
            text-transform: none;
            line-height: 2;
            font-size: 20px;
        }
        &:hover {
            h3 {
                color: $color; 
            }
        }
    }
}

/* 2. Blog */
.blog {
    &__inner {
        display: flex; 
    }
    &__left {
        width: 60vw;
        margin: 0 50px;
        &-item {
            margin-bottom: 100px;
            text-transform: none;
            h2 {
                text-transform: uppercase;
            }
            span {
                color: $color;
            }
            img {
                margin-top: 20px;
                width: 100%;
                height: 400px;
            }
            p {
                line-height: 1.8;
                text-align: justify;
            }
        }
        &-span {
            display: flex;
            justify-content: space-between;
        }
    }
    &__right {
        width: 40vw;
        &-item {
            display: flex;
            align-items: center;
            margin: 0 10px 50px 0;
            img {
                margin-right: 20px;
                width: 150px;
                height: 100px;
            }
        }
        span {
            background-color: $color;
            padding: 10px;
        }
    }
    &__btn {
        margin-left: 50px;
    }
}

/*  3. Travel */
.travel {
    .slider-arrows {
        top: 125px;
        opacity: .5;
    }
    &-slider {
        &__head {
            background-repeat: no-repeat;
            background-position: top left;
            min-height: 780px;
        }
        &__subtitle {
            text-align: right;
            padding-right: 132px 80px 0 0;
        }
        &__btn {
            text-align: center;
        } 
        &__info {
            display: flex;
            justify-content: space-around; 
            &-item {
                min-height: 200px;
                margin-bottom: 66px;
                position: relative; 
            }
            &-subtitle {
                position: absolute;
                font-size: 18px;
                line-height: 22px;
                letter-spacing: 8px;
                opacity: 0.5;
                transform: rotate(-90deg);
                top: 97px;
                left: -97px;
                width: 200px;
                height: 0;
                white-space: nowrap;
                text-align: right;
            }
            &-title {
                font-weight: 800;
                font-size: 40px;
                line-height: 36px;
                padding-left: 40px;
                position: relative; 
                .travel-pricing__span {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 36px;
                    text-align: right;
                    letter-spacing: 5.25px;
                    opacity: 0.75;
                    position: absolute;
                    bottom: -30px;
                    right: 0;
                }
            }
        }
        &__text {
            width: 50vw;
            margin: 0 auto;
            line-height: 2;
            text-align: justify;
            text-transform: none;
            margin-bottom: 50px;
        }
    }
}

/* 4. Travel */
.team {
    &-box {
        &__inner-social {
            display: none; 
        }
    }
    &-box:hover {
        .team-box__inner-social {
            display: block;
            padding: 30px 0 0 30px;
            img {
                width: 40px;
                height: 40px;
                margin-right: 30px;
            }
        }
    }
}

/*  5. Hotel */
.hotel { 
    &-slider {
        &__btn {
            text-align: center;
        }
        &__info {
            display: flex;
            justify-content: space-around;
            margin-top: 100px;
            &-item { 
                min-height: 150px;
                width: 250px;
                margin-bottom: 66px;
                position: relative;
            }
            &-subtitle {
                position: absolute;
                font-size: 18px;
                line-height: 22px;
                letter-spacing: 8px;
                opacity: 0.5;
                transform: rotate(-90deg);
                top: 97px;
                left: -97px;
                width: 200px;
                height: 0;
                white-space: nowrap;
                text-align: right; 
            }
            &-title {
                font-weight: 800;
                font-size: 40px;
                line-height: 36px;
                padding-left: 40px;
                position: relative;
            }
        }
    }
    .slider-arrows {
        top: 720px;
        // width: 40px;
    }
}

.quantity {
    display: inline-block;
    input {
        width: 60px;
        height: 42px;
        display: block;
        border: none;
        background-color: transparent;
        font-weight: 800;
        font-size: 40px;
        line-height: 36px;
        color: #fff;
        &:focus {
            outline: 0; 
        }
    }
    &-nav {
        float: left;
        position: static;
        height: 42px;
    }
    &-button {
        position: relative;
        cursor: pointer;
        width: 20px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 45px;
    }
    &-button.quantity-up {
        position: absolute;
        right: 0;
    }
    &-button.quantity-down {
        position: absolute;
        bottom: -1px;
        right: 25px;
    }
}

/*  6. Contact */
.contact {
    position: relative;
    margin-bottom: 100px;
    &-container {
        width: 100%;
        padding-top: 100px;
    }
    &__form {
        text-align: center;
        width: 50%;
        margin: 0 auto;
        padding: 40px;
        border: 1px solid rgb(41, 41, 41);
        ::placeholder {
            color: #fff;
        }
    }
    &__input {
        position: relative;
        width: 100%;
        margin-top: 10px;
        input,
        textarea {
            width: 80%;
            padding: 15px;
            margin: 10px 0;
            background: rgb(41, 41, 41);
            border: none;
            outline: none;
            resize: none;
            color: #fff;
        }
        input[type="submit"] {
            width: 100px;
            border: none;
            cursor: pointer;
            padding: 10px; 
        }
    }
}