/*  
* Main Slider 
*/

.slider {
    &-item {
        height: 100vh;
        &__info {
            position: absolute;
            right: 50px;
            bottom: 115px;
            text-align: right; 
            &-subtitle {
                margin-bottom: 5px; 
            }
            &-title {
                font-weight: 800;
                font-size: 100px;
                margin-bottom: 48px; 
            }
            &-subtext {
                margin-bottom: 8px;
            }
            &-text {
                font-weight: 800;
                font-size: 40px;
                line-height: 46px;
                margin-bottom: 20px; 
            }
            &-number {
                font-size: 30px;
                display: none;
            }
        }
    }
    &-arrows {
        position: absolute;
        bottom: 100px;
        right: 50px;
        cursor: pointer;
        z-index: 4;
        &-left {
            right: 125px; 
        }
    }
    &-dotshead {
        position: absolute;
        right: 25px;
        bottom: 36px;
        max-width: 920px;
        width: 100%;
        .slick-current {
            .dots-box {
                border-top: 4px solid $color;
            }
            .dots-box__slider {
                display: block;
            }
        } 
    }
}
.dots-box {
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 28px;
    border-top: 4px solid #fff;
    padding-top: 6px;
    &__number {
        font-weight: 800;
        font-size: 24px;
        line-height: 34px;
    }
    &__name {
        font-weight: 300;
        line-height: 19px;
        text-transform: capitalize;
    }
}