/*  
* Aside 
*/

.header {
    position: relative;
    height: 100vh;
    &__aside {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 5;
        border-right: 1px solid rgba(#fff, .25);
        height: 100vh;
        text-align: center;
        min-width: 100px;
    }
    &__title {
        font-weight: 800;
        font-size: 200px;
        line-height: 156px;
        color: #fff;
        opacity: 0.07;
        max-width: 467px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 4;
    }
    &__map {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 3;
        svg {
            width: 500px;
            height: 250px;
        }
    }
    &__arrows {
        position: absolute;
        left: 140px;
        bottom: 30px;
        z-index: 3;
    }
    &__slider {
        &-item {
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            height: 100vh;
            position: relative;
        }
    }
}


.menu {
    padding: 100px 0;
    a {
        img {
            width: 60px;
            height: 60px;
        }
        span {
            display: block;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 3px;
            padding-top: 6px;
            margin-bottom: 50px;
            text-transform: capitalize;
        }
    }
    &-btn {
        display: none;
        width: 40px;
        height: 40px;
        position: absolute;
        z-index: 20;
        right: 20px;
        top: 20px;
        &__line {
            width: 40px;
            height: 5px;
            margin-bottom: 5px;
            background-color: #fff;
        }
    }
}

.line-animation {
    stroke-dashoffset: 600;
    stroke-dasharray: 600;
    animation: animdash 5s infinite 6s;
}
@keyframes animdash {
    to {
        stroke-dashoffset: 0;
    }
}