/*  
* Global 
*/

$color: #ff8f33;

html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
body {
    font-family: 'Fira Sans', sans-serif;
    font-weight: 400;
    font-size: 16px;
    background-color: #171919;;
    color: #fff;
    text-transform: uppercase;
}
a {
    display: inline-block;
    text-decoration: none;
    color: #fff;
}
ul,
li {
    list-style: none;
    margin: 0;
    padding: 0;
}
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}
.section {
    padding: 100px 0;
}
.headline-left {
    font-size: 22px;
    line-height: 32px;
    letter-spacing: 7.33333px;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.5;
    padding-left: 105px;
    position: relative;
    display: inline-block;
    &::before{
        content:"";
        position: absolute;
        width: 80px;
        height: 1px;
        background-color: #fff;
        left: 0;
        top: 50%;
    }
}
.title {
    margin-bottom: 50px;
    span {
        font-weight: 800;
        font-size: 200px;
        color: rgba(#FFFFFF, .02);
        position: relative;
        transform: translateX(-50px);
        display: inline-block;
    }
    h3 {
        position: absolute;
        top: 0;
        right: -5px;
        margin: 0;
        color: #fff;
        font-size: 80px;
        &::after{
            content: "";
            position: absolute;
            width: 80px;
            height: 1px;
            background-color: $color;
            top: 0;
            right: 0;
        }
    }
}
.btn {
    font-weight: 800;
    font-size: 40px;
    line-height: 36px;
    span {
        padding: 17px 57px 17px 17px;
        background-color: $color;
        display: inline-block;
        position: relative;
        &::before{
            content:"";
            position: absolute;
            width: 20px;
            height: 1px;
            top:50%;
            left: -10px;
            background-color: #fff;
        }
        &::after{
            content:"";
            position: absolute;
            width: 25px;
            height: 21px;
            background-image: url(../images/arrow-right.svg);
            top: 50%;
            transform: translateY(-50%);
            right: 17px;
        }
    }
}
.button {
    border: none;
    padding: 15px;
    background-color: $color;
    color: #fff;
    text-transform: none;
}